.book {
    width: 170vw;
    margin-left: -40vw;
      /* max-width: 100%; */
    height: 80vh;
  /* max-height: 70vw; */
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -55%);
  perspective: 1500px;
  user-select: none;
}

.page {
  width: 49%;
  height: 98%;
  padding: 20px;
  position: absolute;
  top: 1%;
  left: 50%;
  font-size: 16px;
  line-height: 18px;
  color: black;
  background-color: blanchedalmond;
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.537) 0%, rgba(0, 0, 0, 0) 20%);
  /* border-radius: 10px; */
  transition: transform 0.5s;
  transform: rotateY(-180deg);
  backface-visibility: hidden;
  transform-style: preserve-3d;
  transform-origin: left center;
  z-index: 4;
}
.page:nth-of-type(2n) {
  left: 1px;
  background-image: linear-gradient(to left, rgba(0, 0, 0, 0.12) 0%, rgba(0, 0, 0, 0) 10%);
  transform-origin: right center;
  transform: rotateY(0);
}
.page:nth-of-type(2n):not(.cover) {
  left: calc(1% + 1px);
}
.page h2 {
  text-align: center;
  font-size: calc(10px + 1vw);
  /* font-family: 'Raleway', sans-serif; */
  font-family: 'Bubblegum Sans', cursive;

}
.page h2 + p::first-letter {
  font-size: 44px;
  
  float: left;
  height: 44px;
  line-height: 36px;
  vertical-align: baseline;
  margin-right: 6px;
  border-radius: 5px;
}

input[type=radio] {
  /* display: none; */
}

input:checked ~ .page {
  transform: rotateY(0);
}
input:checked ~ .page:nth-of-type(2n) {
  transform: rotateY(180deg);
}
input:checked ~ .page:nth-of-type(2n-1) {
  z-index: 3;
}
input:checked ~ .page:nth-of-type(2n-1) ~ .page:nth-of-type(2n-1) {
  z-index: 1;
}
input:checked + .page + .page + input + .page {
  z-index: 2 !important;
}

