}
.cover:first-of-type, .cover:last-of-type {
  background-image: none;
}
.cover:first-of-type::before, .cover:last-of-type::before {
  content: "";
  width: calc(100% - 20px);
  height: calc(100% - 20px);
  border: 2px dashed rgba(0, 0, 0, 0.2);
  position: absolute;
  top: 9px;
  left: 9px;
}

