@keyframes slideU {
  0% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(-16%);
  }
  100% {
      transform: translateX(0);
    }
}
.page:hover .arrow-page{
  /* color:white; */
  animation-name: slideU;
  animation-duration: 2s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  cursor:pointer;
}