  .j-btn{
    cursor:pointer;
    background-color: #3E3F41;
    font-size: calc(10px + 1vw);
    width:  calc(16px + 14vw);;
text-align: center;
    border-radius: 2vw;
    padding: 10px 12px;
    padding-block: 1vw;
    color: #FFE96E ;
