    /* height: 100vh; */
    bottom :0px
  
}
  
  video {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .controls-container {
    position: absolute;
    bottom: 8px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .control-button {
    /* background-color: #333; */
    /* color: #fff; */
    border: none;
    border-radius: 50%;
    width: 60px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 10px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .control-button:hover {
    background-color: #555;
  }
  
  .fa-play,
  .fa-pause,
  .fa-backward,
  .fa-forward {
    color:black;
    font-size: 4rem;
  }
  